import React, { useState, useEffect } from 'react';
import { Calendar, theme } from 'antd';
import { Client } from '@notionhq/client';

const notion = new Client({ auth: process.env.REACT_APP_NOTION_TOKEN });

const fetchTableData = async () => {
  const databaseId = process.env.REACT_APP_NOTION_DATABASE_ID;
  const response = await notion.databases.query({
    database_id: databaseId,
  });
  return response.results;
};

const onPanelChange = (value, mode) => {
  console.log(value.format('YYYY-MM-DD'), mode);
};

const App = () => {
  const { token } = theme.useToken();
  const [data, setData] = useState([]);

  useEffect(() => {
    fetchTableData().then(setData);
  }, []);

  const wrapperStyle = {
    width: 300,
    border: `1px solid ${token.colorBorderSecondary}`,
    borderRadius: token.borderRadiusLG,
  };

  return (
    <div style={wrapperStyle}>
      <Calendar fullscreen={false} onPanelChange={onPanelChange} />
      <ul>
        {data.map((item) => (
          <li key={item.id}>{item.properties.Name.title[0].text.content}</li>
        ))}
      </ul>
    </div>
  );
};

export default App;
